body {
  background: #f8f8f8;
  font-family: Microsoft JhengHei;
  font-weight: 500;
}

.wrap {
  max-width: 800px;
  margin: 30px auto;
}

.wrap ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.wrap ul li {
  width: 31.333%;
  margin-right: 2%;
  flex: 0 0 30.333%;
  margin-right: 2%;
  box-shadow: 0 0 10px #ddd;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.wrap ul li .prod_img {
  width: 80%;
  margin: 0 auto;
  padding: 15px 0;
  overflow: hidden;
  border-radius: 10px 10px 0 0;
}

.wrap ul li .prod_img img {
  width: 100%;
}

.wrap ul li .prod_wrap {
  padding: 10px;
  color: #000;
}

.wrap ul li .prod_wrap .prod_name {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 5px;
  border-bottom: 1px solid #ffae00;
  color: #ffae00;
  margin-bottom: 15px;
}

.wrap ul li .prod_wrap .prod_discr {
  font-size: 16px;
  margin-bottom: 20px;
}

.wrap ul li .prod_wrap .prod_footer {
  display: flex;
  justify-content: space-between;
  color: #b61313;
  align-items: flex-end;
}

.wrap ul li .prod_wrap .prod_footer span, .wrap ul li .prod_wrap .prod_footer strong {
  font-size: 20px;
  font-weight: 600;
  margin-left: 5px;
}

.wrap ul li .prod_wrap .prod_footer .prod_num span {
  font-size: 24px;
}

.wrap ul li .prod_wrap .prod_footer .prod_price span {
  font-size: 24px;
}

@media screen and (max-width: 560px) {
  .wrap {
    max-width: 90%;
    margin: 30px auto;
  }
  .wrap ul li {
    width: 100%;
    flex: 0 0 100%;
    margin-right: 0%;
  }
}
